// Code to keep in index.js
import 'regenerator-runtime/runtime';
import PokeBreeder from './PokeBreeder.js';

const pokeBreeder = new PokeBreeder('pokebreeder');
const goBtn = document.getElementById('startBtn');
const infoBtn = document.getElementById('info');
const closeBtn = document.getElementById('closeBtn');
const infoText = document.getElementById('infoText');
var myEvents = ['mouseup','touchend'];

window.addEventListener('load', () => {
    goBtn.onclick = () => {pokeBreeder.init()};

    myEvents.forEach(evt => 
    infoBtn.addEventListener(evt, () => {
        infoText.classList.remove('hidden');
    }),false);

    myEvents.forEach(evt => 
        closeBtn.addEventListener(evt, () => {
            infoText.classList.add('hidden');
        }),false);
})