import BreedBox from './BreedBox.js';
import Pokemon from './Pokemon.js';
import PokeView from './PokeView.js';
import * as lsHelpers from './ls.js';
import * as utils from './utilities.js';
import * as pokeDex from './PokemonList.json';

var firstParent = null;
var secondParent = null;
var myEvents = ['mouseup','touchend'];

export default class PokeBreeder {
    constructor(parent) {
        this.parent = document.getElementById(parent);
        this.body = null;
        this.backBtn = null;
        this.head = null;
        this.key = this.parent.id;
        this.breedBox = new BreedBox(parent);
        this.pokeList = new Pokemon;
        this.pokeView = new PokeView;
        this.pokeDex = pokeDex;
    }
    init(){
        //for testing ONLY
        //localStorage.clear();
        //for testing ONLY
        console.log('successfully loaded PokeBreeder');
        this.parent.innerHTML = '';
        this.parent.classList.add(this.parent.id);
        this.pokeView.renderStructure(this.parent);
        this.head = document.getElementById('mainHead');
        this.body = document.getElementById('mainBody');
        this.backBtn = document.getElementById('backBtn');
        this.addListeners(this.body);
        this.breedBox.init(this.key);
        this.viewTypes();
    }
    //function to show pokemon types
    async viewTypes(){
        const typeList = await this.pokeList.getAllTypes();
        this.pokeView.renderTypeList(typeList, this.body);
        this.typeListeners('typeList');
        this.buildBackButton(1);
        this.buildNextButton(1);
        this.updateCount();
        let count = this.breedBox.getCurrentCount();
        if(count > 0){
            this.pokeView.renderBasicList('savedList', this.breedBox.getSavedList());
        }     
        this.backBtn.classList.add('hidden');
    }
    async sortByType(type){
        let filteredTypeList = lsHelpers.readFromLS(type);
        if(filteredTypeList == null){
            this.body.innerHTML = `<h3>Loading ${utils.capitalizeName(type)} types...</h3>`;
            //retrieve pokemon info
            const thisType = await this.pokeList.getPokemonByType(type);
            console.log(thisType);
            //list from API of pokemon needed
            let thisTypeList = [];
            thisType.pokemon.forEach(item => thisTypeList.push(item));
            //list to fill with objects from local library
            let theseTypeMons = [];
            
            thisTypeList.forEach(mon => {
                let thisID = mon.pokemon.url.split('/');
                thisID = thisID[thisID.length - 2];
                if (thisID < 808){
                    let thisMon = this.pokeDex.pokemon.find(poke => poke.id == thisID);
                    theseTypeMons.push(thisMon);
                }
            })
            console.log(theseTypeMons);
            lsHelpers.writeToLS(type, theseTypeMons);
            this.pokeView.renderPagedList(type, utils.sortByName(theseTypeMons), this.body);
        } else {
            this.pokeView.renderPagedList(type, utils.sortByName(filteredTypeList), this.body);
        }
    }

    typeListeners(parent){
        const parentElement = document.getElementById(parent);
        myEvents.forEach( evt => 
            parentElement.addEventListener(evt, (e) => {
                this.sortByType(e.target.dataset.id, e.target.dataset.url);
                this.backBtn.classList.remove('hidden');
                e.stopPropagation();
                e.preventDefault();
              }), false);
    }
    addListeners(parent){
        myEvents.forEach(evt => 
        parent.addEventListener(evt, e => {
            if (e.target.classList.contains('add-button')){
                let count = this.breedBox.getCurrentCount();
                if (count < 12){
                    if(e.target.dataset.gender && e.target.dataset.gender != 'd') {
                    console.log(`add ${e.target.dataset.type} type ${e.target.dataset.gender} pokemon ${e.target.dataset.id}`);
                    this.breedBox.addToBox(e.target.dataset.type, e.target.dataset.id, e.target.dataset.gender);
                    this.pokeView.renderBasicList('savedList', this.breedBox.getSavedList());
                    this.updateCount();
                    this.listListeners();
                }} else {
                    this.pokeView.noAdd('full');
                }
            } else if (e.target.classList.contains('select-button')){
                let parentTwo = lsHelpers.readFromLS('partners').find(poke => poke.id == e.target.dataset.id);
                    parentTwo.gender = e.target.dataset.gender;
                    secondParent = parentTwo;
                    parent.removeChild(document.getElementById('partnerList'));
                    this.pokeView.renderParentOne(parent, firstParent)
                    this.pokeView.renderParentTwo(parent, secondParent);
                    //invoke this after both parents are chosen
                    this.buildNextButton(3);
            }
            e.stopPropagation();
            e.preventDefault();
            }), false);
    }
    listListeners(){
        const removeBtns = Array.from(document.querySelectorAll('.remove'));
        removeBtns.forEach(btn =>{
            myEvents.forEach(evt =>
                btn.addEventListener(evt, e => {
                    this.breedBox.removeFromBox(e.target.dataset.id, e.target.dataset.gender);
                    this.pokeView.renderBasicList('savedList', this.breedBox.getSavedList());
                    this.updateCount();
                    this.listListeners();
                    e.stopPropagation();
                    e.preventDefault();
                }), false);
        })
    }
    parentOneListeners(){
        const parentOne = Array.from(document.querySelectorAll('p.fancy-parent'));
        parentOne.forEach(pokeParent => {
            myEvents.forEach(evt =>
            pokeParent.addEventListener(evt, e => {
                //set global variable for first parent
                firstParent = this.breedBox.getSavedList().find(poke => (poke.id == e.currentTarget.dataset.id && poke.gender == e.currentTarget.dataset.gender));
                this.pokeView.renderParentOne(this.body, firstParent);
                //check if parent is genderless(only Ditto breedable)
                this.findPartners(this.body, firstParent, firstParent.egg_groups);
                this.buildBackButton(2);
                this.backBtn.classList.remove('hidden');
            }), false);
        })
        //console.log(parentOne);
    }

    async findPartners(parentElement, parentOne, groupArray){
        const partnerList = [];
        const heading = document.createElement('h3');
                heading.innerHTML = `Fetching potential partners...`;
                parentElement.appendChild(heading);
        if (parentOne.breed_status === 1){
            //if parent 1 is genderless, only Ditto gets added to the partner list
            partnerList.push(this.pokeDex.pokemon.find(poke => poke.id == 132));
        } else{
        
            //check one or more egg groups to see if they exist and pull or create that data
            for(let g=0; g < groupArray.length; g++) {
                console.log(`egg group ${groupArray[g].name}`);
                //check to see if this egg group's Pokemon have already been saved to LS
                let eggGroupList = lsHelpers.readFromLS(`eg_${groupArray[g].name}`);
                if(eggGroupList == null){
                    //retrieve basic pokemon info
                    const listThisGrp = await this.pokeList.getDetails(groupArray[g].url);
                    //push to an array
                    let thisGrpPokemon = [];
                    listThisGrp.pokemon_species.forEach(mon => thisGrpPokemon.push(mon));
                    //figure out which pokemon we actually want to display
                    eggGroupList = [];
                    thisGrpPokemon.forEach(mon => {
                        let thisID = mon.url.split('/');
                        thisID = thisID[thisID.length - 2];
                        if (thisID < 808){
                        let thisMon = this.pokeDex.pokemon.find(poke => poke.id == thisID);
                            //make sure this pokemon is not a Ditto-only breeder
                            if (thisMon.breed_status > 1){
                                eggGroupList.push(thisMon);
                            }
                        }
                    })
                    //add Ditto to every egg group
                    eggGroupList.push(this.pokeDex.pokemon.find(poke => poke.id == 132));
                    //save egg group to LocalStorage to pull up more easily in future
                    lsHelpers.writeToLS(`eg_${groupArray[g].name}`, eggGroupList);
                }
                //push each Pokemon in the egg group into the main list to be sorted and displayed
                eggGroupList.forEach(mon => {
                    //make sure the pokemon isn't already in the array (for pokemon with multiple egg groups)
                    const checkDuplicate = partnerList.some(poke => poke.id == mon.id);
                    if(!checkDuplicate){partnerList.push(mon);}
                })
            }
        }
        //once the partner list has been populated with pokemon from ALL available egg groups, send it to the list to be rendered  
        heading.innerHTML = `Select Parent 2`;
        lsHelpers.writeToLS('partners',partnerList);
        this.pokeView.renderPartnerList(utils.sortByName(partnerList), parentOne, parentElement);
    }
    buildBackButton(state = 1) {
        console.log(`build back button state ${state}`)
        this.backBtn.innerHTML = '';
        const backButton = document.createElement("button");
        const countBox = document.getElementById('countBox');
        if(state == 1){
            backButton.textContent = "<< Back to Types List";
            myEvents.forEach(evt =>
            backButton.addEventListener(evt, () => {
                this.viewTypes();
                this.buildNextButton(1);
                countBox.style.display = 'flex';
                backButton.classList.add('hidden');
            }), false);
        } else if(state == 2){
            console.log('back to parents');
            backButton.textContent = "<< Back to Parent List";
            myEvents.forEach(evt =>
            backButton.addEventListener(evt, () => {
                this.pokeView.renderParentList(this.body, this.breedBox.getSavedList());
                this.parentOneListeners();
                this.buildNextButton(2)
                this.buildBackButton(1);
                countBox.style.display = 'none';
                this.backBtn.classList.remove('hidden');
            }), false);
        }
        this.backBtn.appendChild(backButton);
    }

    buildNextButton(step = 1){
        const nextBtnDiv = document.getElementById('nextBtn');
        const nextBtn = document.createElement('button');
        const countBox = document.getElementById('countBox');
        if(step == 1){
            nextBtnDiv.innerHTML = '';
            nextBtn.textContent = 'Select Parent 1 >>';
            myEvents.forEach(evt =>
            nextBtn.addEventListener(evt, () => {
                nextBtnDiv.innerHTML = '';
                this.pokeView.renderParentList(this.body, this.breedBox.getSavedList());
                countBox.style.display = 'none';
                this.parentOneListeners();
                this.backBtn.classList.remove('hidden');
            }), false);
            nextBtnDiv.appendChild(nextBtn);
        } else if (step == 2) {
            nextBtnDiv.innerHTML = '';
        } else if (step == 3){
            nextBtnDiv.innerHTML = '';
            nextBtn.textContent = 'View Results >>';
            myEvents.forEach(evt =>
            nextBtn.addEventListener(evt, (e) => {
                this.determineBaby();
                e.stopPropagation();
                e.preventDefault();
            }), false);
            nextBtnDiv.appendChild(nextBtn);
        } else if (step == 4){
            nextBtnDiv.innerHTML = '';
            nextBtn.textContent = 'Start Over?';
            myEvents.forEach(evt =>
            nextBtn.addEventListener(evt, (e) => {
                this.init();
                e.stopPropagation();
                e.preventDefault();
            }), false);
            nextBtnDiv.appendChild(nextBtn);
        }
    }

    updateCount(){
        let count = this.breedBox.getCurrentCount();
        this.pokeView.showCounter(count);
    }

    async determineBaby(){
        this.body.innerHTML = '';
        let evChain;
        //if first parent is genderless (breeding with Ditto), baby's species is the other parent regardless of gender
        if (firstParent.gender == 'genderless' || firstParent.gender == 'female' || secondParent.name == 'ditto'){
            evChain = firstParent.ev_chain;
        } else if (firstParent.gender == 'male'){
            evChain = secondParent.ev_chain;
        }
        //get evolution chain info from API
        evChain = await this.pokeList.getDetails(evChain);
        console.log(evChain);

        //retrieve stored baby info
        let babyInfo = evChain.chain.species.url.split('/');
            babyInfo = this.pokeDex.pokemon.find(poke => poke.id == babyInfo[babyInfo.length - 2]);
            console.log(babyInfo);

        if(babyInfo.trigger_item.name != null){
            //retrieve alternate baby
            const babyTwo = this.pokeDex.pokemon.find(poke => poke.id == babyInfo.evolves_to);

            console.log('creating second baby');
            console.log(babyTwo);

            this.buildNextButton(4);
            this.pokeView.renderResults(this.body, firstParent, secondParent, babyInfo, babyTwo);
        } else {
            this.buildNextButton(4);
            this.pokeView.renderResults(this.body, firstParent, secondParent, babyInfo);
        }
    }
}
