import * as lsHelpers from './ls.js';

export default class BreedBox {
    constructor(parent) {
        this.parent = document.getElementById(parent);
        this.LSkey = `${parent}-bb`;
        this.pokeList = null;
    }
    init(){
        //localStorage.clear();
        console.log(`initializing BreedBox with ${this.LSkey}`)
        this.pokeList = lsHelpers.readFromLS(this.LSkey);
        if (this.pokeList == null){
            this.pokeList = [];
        }
    }
    createPokeObject(apiInfo){
        //console.log(apiInfo);
        const oneMon = {
          'id' : apiInfo.full_details.id,
          'name' : apiInfo.full_details.species.name,
          'img' : apiInfo.full_details.sprites.front_default,
          'types' : apiInfo.full_details.types,
          'breed_status' : apiInfo.full_details.breedable_status,
          'baby' : apiInfo.species_details.is_baby,
          'egg_groups' : apiInfo.species_details.egg_groups,
          'ev_chain' : apiInfo.species_details.evolution_chain.url,
          'gender' : null,
          'hatch_count' : apiInfo.species_details.hatch_counter,
          'gender_rate' : apiInfo.species_details.gender_rate,
          'trigger_item' : {
                'name' : apiInfo.trigger_item ? apiInfo.trigger_item.name : null,
                'img' : apiInfo.trigger_item ? apiInfo.trigger_item.img : null
           },
          'evolves_to' : apiInfo.evolves_to
        }
        return oneMon;
    }
    addToBox(type, id, gender){
        let pokeMon = lsHelpers.readFromLS(type).find(poke => poke.id == id);
        pokeMon.gender = gender;
        const checkDuplicate = this.pokeList.some(poke => (poke.id == id && poke.gender == gender));
        if(!checkDuplicate){
            this.pokeList.push(pokeMon);
            lsHelpers.writeToLS(this.LSkey, this.pokeList);
        }
    }
    removeFromBox(id, gender){
        //let bbArray = lsHelpers.readFromLS(this.LSkey);
        console.log('removeFromBox invoked');
        let oneMon = this.pokeList.findIndex(pokemon => (pokemon.id == id && pokemon.gender == gender));
        this.pokeList.splice(oneMon, 1);
        lsHelpers.writeToLS(this.LSkey, this.pokeList);
    }
    getCurrentCount(){
        if(this.pokeList != null && this.pokeList.length > 0){
            return this.pokeList.length;
        } else {
            return 0;
        }
    }
    getSavedList(){
        return this.pokeList;
    }
}